
.footer{
    padding: 30px 150px;
    color: #fff;
    background-color: #000;
    overflow: hidden;

    @media screen and (min-width:768px) and (max-width:1023px){
      padding: 17px 50px;
    }



.line-1{
    display: flex;
    grid-column-gap: 10px;
    justify-content: space-between;
    align-items: flex-end;
  .logo-link-wrapper{
      justify-self: normal;
    .logo {
      height: 43px;
      width: 89px;
      background-size: contain;
      background-repeat: no-repeat;

    @media screen and (min-width:768px) and (max-width:1023px){
          height: 23px;
          width: 48px;
    }

      &.g {
        background-image: url("../../assets/images/g-level.svg");
      }
      &.b {
        background-image: url("../../assets/images/b-level.svg");
      }
      &.s {
        background-image: url("../../assets/images/s-level.svg");
      }
    }
  }

  .timetable{
    font-family: Raleway;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0.1em;
    display: flex;
    column-gap: 15px;
    flex-wrap: wrap;
    justify-content: center;
    span{
      white-space: nowrap;
    }

    @media screen and (min-width:768px) and (max-width:1023px){
      display: none;

    }
  }

  .address {
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.1em;
    span{
      white-space:nowrap;
    }


  }
  .phone-link{
    flex-shrink: 0;
  }

  .social-networks{
    justify-self: end;
    display: grid;
    grid-template-columns: repeat(2, 16px);
    grid-column-gap: 35px;

    & > a{
      text-decoration: none;
      width: 16px;
      height:16px;

      &.facebook{
        // background-image: url("../../assets/images/facebook.svg");
        &:hover svg > path {
            fill: #916C58;
        }
      }

      &.instagram{
        // background-image: url("../../assets/images/instagram.svg");
        &:hover svg > path {
            fill: #916C58;
        }
      }


    }

  }

  }

  .line-2{
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (min-width:768px) and (max-width:1023px){
      margin-top: 23px;
    }

    .link-1{
      text-align: center;

      color: #FFFFFF;
      opacity: 0.55;
      text-decoration: none;
      font-family: Raleway;
      font-weight: 600;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      transition:opacity .2s ease-in;

      margin-right:60px;

    @media screen and (min-width:768px) and (max-width:1023px){
      font-size: 11px;
      line-height: 13px;
    }

      &:last-of-type{
        margin-right: 0;
      }

      &:hover{
        opacity:1;
      }
    }
  }

hr{
  background: #FFFFFF;
  opacity: 0.15;
  height: 1px;
  margin: 30px 0;

  @media screen and (min-width:768px) and (max-width:1023px){
    margin: 16px 0;
  }
}
  .line-3{
    display: grid;
    grid-template-columns: 1fr 1fr;



    font-family: Raleway;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;

    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #FFFFFF;

    @media screen and (min-width:768px) and (max-width:1023px){
      font-size: 10px;
    }

    .text-1{
    }


    .design-text{
      display: flex;
      align-items: flex-end;
      justify-self: end;

      .design-logo{
        margin-left: 12px;
        height: 23px;
        width: 71px;
        background: url("../../assets/images/design-logo.svg") no-repeat;
      }
    }
  }
}
