
    .selectLang {
        position: relative;
        transition: all 0.2s ease;
    }
    .buttonLang {
        width: 31px;
        height: 16px;
        font-family: Raleway;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: currentColor;
        background: transparent;
        padding: 0 5px;
        border-left: solid 2px #916C58;
        transform: unset;
        &::before {
            transform: none !important;
            transition: none !important;
        }
        &>span{
            transform: unset;
        }
        &:hover{
            filter: none;
            color: #916C58;
        }
    }
    .chooseLang {
        font-family: 'Raleway';
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.05em;
        position: absolute;
        width: 21px;
        text-transform: uppercase;
        list-style-type: none;
        background: #000000;
        border-left: solid 2px #916C58;
        padding: 10px 5px;
        margin-top: 9px;
        & li{
            opacity: 0.75;
            &:hover {
                opacity: 1;
            }
        }
    }

    .dropdown-fade-enter-active, .dropdown-fade-leave-active {
        transition: all 0.2s ease-in-out;
    }

  .dropdown-fade-ente-to, .dropdown-fade-leave-to {
    opacity: 0;
    transform: translateY(-12px);
  }
