

button{
    margin-top: 35px;
    align-self: center;
}

form{
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 767px) {
        width: 80%;
    }
}


.input-wrapper{
    width: 370px;
    height: 69px;
    position: relative;

    @media screen and (max-width: 767px) {
        width: unset;
        height: 50px;
    }
}



.input-wrapper + .input-wrapper {
    margin-top: 30px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
  }


@mixin activeLable{
    color: #916C58;
    margin-bottom: 10px;
    font-size: 15px;
    opacity:1;

    @media screen and (max-width: 767px) {
       font-size: 12px;
    }
}

input{
    position: absolute;
    width: 100%;
    height:25px;
    font-family: Raleway;
    font-weight: 500;
    font-size: 16px;
    color: #fff;
    border: none;
    outline: none;
    background-color: transparent;
    padding: unset;
    border-bottom: 2px solid rgba(255,255,255, .25);
    transition: border-bottom .2s ease-out;

    &:focus {
        border-bottom: 2px solid rgba(255,255,255, 1);
    }

    &:focus, &:not(:placeholder-shown){
         & + label{
             @include activeLable;
         }
    }

    @media screen and (max-width: 767px) {
        font-size: 14px;
    }

}

 label {
    font-family: Raleway;
    position: absolute;
    top: -26px;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    opacity: 0.55;
    transition:all .2s ease-out;

    @media screen and (max-width: 767px) {
        font-size: 16px;
        line-height: 18px;
        top: -17px;
    }
}

.error-message{
    color: #916C58;
    position: absolute;
    top: 30px;
    right:0;
    transition: opacity .2s ease-out;

    font-family: Raleway;
    font-weight: 500;
    font-size: 14px;
}
