
    .team-g {
        padding-top: 208px;
        background-color: #FFFFFF !important;
    }
    .title {
        padding: 0 7.81vw;
        margin-bottom: 60px;
    }
    .title > h1 {
        font-weight: 600;
        font-style: normal;
        font-family: 'Oswald';
        font-size: 52px;
        margin: 0;
        margin-left: 96px;
        margin-bottom: 30px;
        line-height: 54px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .title > p {
        font-weight: 400;
        font-style: normal;
        font-size: 18px;
        margin-bottom: 10px;
        font-family: 'Raleway';
        font-style: normal;
        font-weight: normal;
        line-height: 32px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .title > b {
        font-style: normal;
        font-size: 18px;
        font-family: 'Raleway';
        font-style: normal;
        font-weight: bold;
        line-height: 32px;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .tickets {
        grid-template-columns: repeat(3, 321px);
        grid-column-gap: 64px;
        display: grid;
        justify-content: center;
        padding: 0 7.81vw;
        transition: transform .2s ease-in;

        .level{
            transition: transform 0.2s ease-in;
            // &:hover{
            //     transform: scale(1.1);
            // }
        }

        & p {
            text-transform: uppercase;
            margin: 0;
            margin-bottom: 20px;
            padding-left: 10px;
            font-family: Oswald;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 36px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #916C58;
        }
        & .imgDiv {
            width: 100%;
            overflow: hidden;
            margin-bottom: 30px;
            & img {
                max-width: 321px;
                width: 100%;
                height: auto;
                cursor: pointer;
                transition: transform .5s ease-in;
                &:hover{
                    transform: scale(1.3);
                }
            }
        }

        & .detail {
            font-family: Raleway;
            font-weight: bold;
            font-size: 12px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            text-decoration: none;
            color: #000;
            display: flex;
            align-items: center;
            margin-bottom: 60px;
            padding-right: 20px;
            float: right;
            cursor: pointer;
            &:active{
                color: #916C58;
            }

            &:after{
                content: '';
                width: 31px;
                height:16px;
                background: url("../../assets/homeView/arrow-right.svg") no-repeat;
                margin-left: 15px;
            }
        }
    }
    .ticket-title {
        font-family: Oswald;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        text-align: center;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #916C58;
        margin: 0 auto;
        margin-bottom: 28px;
        width: 100%;
        & span {
            text-transform: uppercase;
        }
    }
    .ticket-discript {
        width: 100%;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 25px;
        display: flex;
        margin-bottom: 50px;
        & img {
            max-width: 321px;
            width: 100%;
            margin-left: 20px;
        }
        & .ticket-period {
            width: 100%;
            & p {
                margin: 0;
                line-height: 22px;
                font-family: Raleway;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                letter-spacing: 0.05em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #000000;
            }
        }
        & .ticket-period-price{
            display: flex;
            grid-column-gap: 25px;
            align-items: center;
            margin-top: 20px;
            & p{
               font-family: Raleway;
                font-style: normal;
                font-weight: 800;
                font-size: 18px;
                line-height: 28px;
                letter-spacing: 0.05em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #916C58;
                margin: 0;
            }
            & button {
                width: 200px;
                height: 46px;
                & span {
                   font-family: Raleway;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 16px;
                    letter-spacing: 0.1em;
                    text-transform: uppercase;
                    color: #FFFFFF;
                }
            }
        }
    }
    .ticket-detail {
        list-style: none;
        padding: 0;
        padding-left: 1em;
        background: rgba(244,240,238, .05);
        & li {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 28px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }

        & li::before {
            content: "\25A0";
            color: #916C58;
            font-weight: bold;
            font-size: 10px;
            display: inline-block;
            width: 1.5em;
            margin-left: -1.5em;
        }
    }

    .popup-tickets-enter-active,
    .popup-tickets-leave-active {
        transition: all .2s ease;
    }
    .popup-tickets-enter-from,
    .popup-tickets-leave-to {
        opacity: 0;
    }
    @media screen and (max-width: 1123px) and (min-width: 768px) {
        .team-g {
            padding-top: 108px;
            height: 65%;
        }
        .title {
            padding: 0 50px;
            margin-bottom: 40px;
        }
        .title > h1 {
            font-size: 49px;
            margin-left: 57px;
            margin-bottom: 20px;
            line-height: 54px;
        }
        .title > p {
            font-size: 16px;
            margin-bottom: 15px;
            line-height: 26px;
        }
        .title > b {
            font-size: 16px;
            line-height: 26px;
        }
        .tickets {
            grid-template-columns: repeat(3, 171px);
            grid-column-gap: 57px;
            justify-content: center;
            padding: 0 50px;
            & p {
                margin-bottom: 20px;
                padding-left: 10px;
                font-size: 22px;
                line-height: 32px;
            }
            &>.imgDiv>img {
                max-width: 171px;
                width: 100%;
                height: auto;
                margin-bottom: 20px;
            }
            & .detail {
                padding-right: 10px;
            }
        }
        .ticket-title {
            font-size: 22px;
            line-height: 33px;
        }
        .ticket-discript {
            & img {
                max-width: 268px;
            }
            & .ticket-period {
                width: 100%;
                & p {
                    font-size: 12px;
                }
            }
            & .ticket-period-price{
                grid-column-gap: 20px;
                & p{
                    font-size: 16px;
                    line-height: 22px;
                }
                & button {
                    width: 200px;
                    height: 46px;
                }
            }
        }
        .ticket-detail {
            & li {
                font-size: 12px;
                line-height: 22px;
            }
            & li::before {
                font-size: 8px;
            }
        }
    }
    @media screen and (max-width: 767px){
        .team-g {
            padding-top: 108px;
        }
        .title {
            padding: 0 15px;
            margin-bottom: 50px;
        }
        .title > h1 {
            font-size: 32px;
            margin-left: 50px;
            margin-bottom: 20px;
            line-height: 48px;
        }
        .title > p {
            font-size: 14px;
            margin-bottom: 20px;
            line-height: 22px;
        }
        .title > b {
            font-size: 14px;
            line-height: 22px;
        }
        .tickets {
            grid-template-columns: repeat(1, 290px);
            justify-content: center;
            padding: 0 15px;
            & p {
                margin-bottom: 20px;
                padding-left: 10px;
                font-size: 18px;
                line-height: 27px;
            }
            &>.imgDiv>img {
                max-width: 290px;
                width: 100%;
                height: auto;
                margin-bottom: 30px;
            }
            & .detail {
                padding-right: 10px;
            }
        }
        .ticket-title {
            font-size: 18px;
            line-height: 27px;
            margin-top: 40px;
        }
        .ticket-discript {
            position: relative;
            left: -250px;
            & img {
                max-width: 268px;
            }
            & .ticket-period {
                width: 100%;
                & p {
                    font-size: 12px;
                }
            }
            & .ticket-period-price{
                grid-column-gap: 20px;
                display: inline-block;
                & p{
                    font-size: 16px;
                    line-height: 22px;
                    margin-bottom: 30px;
                }
                & button {
                    width: 200px;
                    height: 46px;
                }
            }
        }
        .ticket-detail {
            padding: 0 15px;
            // overflow: auto;
            // max-height: 320px;
            height: 100%;
            & li {
                font-size: 12px;
                line-height: 18px;
            }
            & li::before {
                font-size: 8px;
            }
        }
    }

