

$color-gold-light: #f4f1ef;
$color-gold-light2: #ebe4e1;

.schedule-page{
    position: relative;
    z-index: 1;
    padding-top: 208px;
    background-color: #FFFFFF !important;
    margin-bottom: 60px;
    padding-left: 7.81vw;
    padding-right: 7.81vw;
}
.schedule-section {
    overflow-x: auto;
    padding: 0 15px;
}


.title {
    padding: 0 7.81vw;
    margin-bottom: 30px;
}

.title > h1 {
    font-weight: 600;
    font-style: normal;
    font-family: 'Oswald';
    font-size: 52px;
    margin: 0;
    margin-bottom: 30px;
    line-height: 54px;
    letter-spacing: 0.02em;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #000000;
}

.schedule-table.schedule-table-mobile{
    display: none;
}
.schedule-table.schedule-table-desktop{
    display: grid;
}

.schedule-wrapper {
    //padding: 0 58px 0 9.81vw;
    //margin-bottom: 60px;

    margin-bottom: 60px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    &.schedule-wrapper-desktop{
        width: 1140px;
    }


    .schedule-table-head {
        border-top: 1px solid #000000;
        border-bottom: 1px solid #000000;

        .schedule-table-column {
            font-weight: bold;
            text-transform: uppercase;
            height: 55px;
            padding-top: 8px;
            grid-template-rows: 30px 16px;
            position: relative;

            //&:first-child{
            //    grid-template-rows: auto;
            //}

            > .day-date {
                display: block;
                text-transform: lowercase;
                font-size: 12px;
            }
        }
    }

    .schedule-table-row {
        display: grid;
        grid-template-columns: 72px repeat(6, 178px);

        .schedule-table-column {
            text-align: center;
            //display: grid;
            align-items: center;
        }
    }

    .schedule-table-body {

        .schedule-table-row {
            border-bottom: 1px solid #000;

            .schedule-table-column {
                min-height: 90px;

                .schedule-day-time{
                    height: 90px;
                    display: grid;
                    align-items: center;
                    .time{
                        font-size: 12px;
                        font-weight: bold;
                    }
                }

                .schedule-day-note{
                    background: $color-gold-light;
                }
                &:nth-child(2n) {
                    .schedule-day-note{
                        background: $color-gold-light2;
                    }
                }

                &:first-child {

                    .schedule-day-note{
                        background: none;
                    }
                }
            }
        }

    }
}


@media screen and (max-width: 1123px) and (min-width: 768px) {
    .schedule {
        padding-top: 108px;
    }
    .title {
        padding: 0 50px;
        margin-bottom: 20px;
    }
    .title > h1 {
        font-size: 49px;
        margin-left: 57px;
        margin-bottom: 0;
    }

    .schedule-table.schedule-table-mobile{
        display: none;
    }
    .schedule-table.schedule-table-desktop{
        display: grid;
    }

}

@media screen and (max-width: 767px) {

    .schedule-page{
        padding-top: 120px;
    }

    .schedule-table.schedule-table-mobile{
        display: grid;
    }
    .schedule-table.schedule-table-desktop{
        display: none;
    }
    .schedule-wrapper.schedule-wrapper-mobile{
        .schedule-table.schedule-table-mobile{
            .schedule-table-head{
                .schedule-table-row{
                    grid-template-columns: auto;
                    .schedule-table-column{
                        text-align: left;

                        .open-icon{
                            position: absolute;
                            top: 25px;
                            right: 0;
                            height: 13px;
                            width: 13px;
                            background: url("../../assets/images/open-icon.svg") no-repeat center;
                            color: #916C58;
                            color: #000;
                        }
                        .opened-icon{
                            position: absolute;
                            top: 25px;
                            right: 0;
                            height: 13px;
                            width: 13px;
                            background: url("../../assets/images/opened-icon.svg") no-repeat center;
                            //background-size: cover;
                            color: #000;
                            display: none;
                        }

                        &.is-open{
                            .opened-icon{
                                display: block;
                            }
                            .open-icon{
                                display: none;
                            }
                        }
                    }
                }
            }

            .schedule-table-body{
                max-height: 0;
                overflow: hidden;
                transition: max-height 0.25s ease-out;

                &:first-child{
                    //border-bottom: 1px solid #000000;
                    border-bottom:none;
                }
            }
            .schedule-table-body.is-open{
                max-height: 1000px;
                transition: max-height 0.25s ease-in;
            }
        }

        .schedule-table.schedule-table-mobile{
            .schedule-table-body{
                .schedule-table-row{
                    grid-template-columns: 72px auto;
                }
            }
        }
    }

    .schedule {
        padding-top: 108px;
    }
    .title {
        padding: 0 15px;
        margin-bottom: 20px;
    }
    .title > h1 {
        font-size: 32px;
        margin-left: 48px;
        margin-bottom: 0;
    }
}

