
.view-area-g{
  background-color: #fff;
  padding: 208px 0 60px;
  @media screen and (max-width:767px){
            padding: 110px 0 60px;
        }
        @media screen and (max-width: 1023px) and (min-width: 768px) {
            padding: 110px 0 60px;
        }  

    .title{
        font-family: Oswald;
        font-weight: 600;
        font-size: 52px;
        line-height: 54px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
        margin-bottom: 30px;
        margin-left: 216px;
        @media screen and (max-width:767px){
            margin-bottom: 20px;
            font-size: 32px;
            line-height: 48px;
            margin-left: 65px;
        }
        @media screen and (max-width: 1023px) and (min-width: 768px) {
            font-size: 45px;
            line-height: 54px;
        }
    }
    .description{
        padding: 0 150px;
        font-family: Raleway;
        font-size: 14px;
        line-height: 28px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
        @media screen and (max-width:767px){
            font-size: 12px;
            line-height: 18px;
            padding: 0 15px;
        }
        @media screen and (max-width: 1023px) and (min-width: 768px) {

        }
    }

    .big-image{
        width: 88%;
        margin-left: auto;
        display: block;
        margin-top: 60px;
        margin-bottom: 60px;
        @media screen and (max-width:767px){
            margin-left: unset;
            margin-top: 50px;
            margin-bottom: 30px;
            width: 100%;
            font-size: 12px;
            line-height: 18px;
        }
        @media screen and (max-width: 1023px) and (min-width: 768px) {

        }
    }

    .gallery-slider {
        position: relative;
        padding: 0 7.81vw;
    }

    .open-popup-container{
        width: max-content;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;

        .open-popup-title{
            font-family: Oswald;
            font-weight: 600;
            font-size: 64px;
            line-height: 64px;
            text-align: center;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
            @media screen and (max-width:767px){
                font-size: 30px;
                line-height: 36px;
            }
            @media screen and (max-width: 1023px) and (min-width: 768px) {
                font-size: 45px;
                line-height: 54px;
            }
        }

        .open-popup-text{
            margin: 30px 0;
            font-family: Raleway;
            font-size: 14px;
            line-height: 28px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
}

