
    .simulator-g {
        padding-top: 208px;
        background-color: #FFFFFF !important;
        .logo {
            background-image: url("../../assets/images/technogym-logo-black.svg");
            background-repeat: no-repeat;
            background-size: contain;
            height: 84px;
            width: 310px;
            margin-bottom: 30px;
            margin-top: 20px;
            margin-left: auto;
        }
    }
    li {
        transform: none !important;
    }
    .title {
        padding: 0 7.81vw;
        margin-bottom: 60px;
        & > h1 {
            font-weight: 600;
            font-style: normal;
            font-family: Oswald;
            font-size: 52px;
            margin: 0;
            margin-left: 96px;
            margin-bottom: 30px;
            line-height: 54px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
        & > p {
            font-weight: 400;
            font-style: normal;
            font-size: 18px;
            margin-bottom: 10px;
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            line-height: 32px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
    .simulator-direction {
        font-family: Oswald;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 27px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        padding: 0 7.81vw;
        padding-bottom: 60px;
        & > .direction {
            list-style-type: none;
            display: flex;
            color: #000000;
            grid-column-gap: 40px;
            align-items: center;
            text-align: center;
            padding: 0;
            cursor: pointer;
            border-bottom: solid 2px #000000;
            text-align: left;
            & > li {
                padding-bottom: 20px;
                position: relative;
                white-space: nowrap;
                &:hover {
                    color: #916C58;
                }
            }
        }
        .invert-color{
            color: #916C58;
            &::before {
                content: "";
                color: #916C58;
                position: absolute;
                bottom: -2px;
                top: 0px;
                left: 0;
                right: 0;
                border-bottom: 2px solid #916C58;
            }
        }
        & .simulator{
            list-style-type: none;
            overflow: visible;
            grid-template-columns: repeat(4, 1fr);
            grid-column-gap: 15px;
            grid-row-gap: 40px;
            display: grid;
            padding: 0;
            & li {
                cursor: pointer;
            }
            & li img {
                display:block;
                width:100%;
            }
            & li div{
                font-family: Oswald;
                font-style: normal;
                font-weight: 600;
                font-size: 18px;
                line-height: 27px;
                letter-spacing: 0.02em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #000000;
                padding: 10px 5px 0 5px;
            }
        }
    }
    @media screen and (max-width: 1023px) and (min-width: 768px){
        .title {
            padding: 0 15px;
            margin-bottom: 20px;
            & > h1 {
                font-size: 49px;
                margin-left: 50px;
                margin-bottom: 20px;
                line-height: 54px;
            }
            & > p {
                font-size: 16px;
                line-height: 26px;
            }
        }
        .simulator-direction {
            font-size: 14px;
            padding: 0 15px;
            & > .direction {
                grid-column-gap: 20px;
                flex-wrap: wrap;
                border-bottom: none;
                & li {
                    padding-bottom: 5px;
                    &:hover {
                        color: #916C58;
                    }
                }
            }
            .invert-color{
                color: #916C58;
                border-bottom: solid 2px #916C58;
            }
            & .simulator {
                grid-template-columns: repeat(3, 1fr);
                grid-column-gap: 10px;
                grid-row-gap: 30px;
            }
        }
    }
    @media screen and (max-width: 767px){
        .simulator-g {
            padding-top: 108px;
            .logo {
                height: 38px;
                width: 140px;
                margin-bottom: 20px;
            }
        }
        .title {
            padding: 0 15px;
            margin-bottom: 20px;
            & > h1 {
                font-size: 32px;
                margin-left: 50px;
                margin-bottom: 20px;
                line-height: 48px;
            }
            & > p {
                font-size: 14px;
                line-height: 22px;
            }
        }
      .simulator-direction {
        font-size: 14px;
        padding: 0 15px;
        & > .direction {
            grid-column-gap: 20px;
            flex-wrap: wrap;
            border-bottom: none;
            & li {
                padding-bottom: 5px;
                &:hover {
                    color: #916C58;
                }
            }
        }
        .invert-color{
            color: #916C58;
            border-bottom: solid 2px #916C58;
        }
        & .simulator {
            grid-template-columns: repeat(2, 1fr);
            grid-column-gap: 10px;
            grid-row-gap: 30px;
            & li img {
                max-width: 140px;
            }
            & li div{
                font-size: 12px;
                line-height: 21px;
                padding: 5px 0;
            }
        }
      }
    }

    .popup-detail-enter-active,
    .popup-detail-leave-active {
        transition: all .2s ease;
    }
    .popup-detail-enter-from,
    .popup-detail-leave-to {
        opacity: 0;
    }


