
    .team-g {
        padding-top: 208px;
        background-color: #FFFFFF !important;
        .title {
            padding: 0 7.81vw;
            margin-bottom: 60px;
        }
        .title > h1 {
            font-weight: 600;
            font-style: normal;
            font-family: 'Oswald';
            font-size: 52px;
            margin: 0;
            margin-left: 96px;
            margin-bottom: 30px;
            line-height: 54px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
        .title > p {
            font-weight: 400;
            font-style: normal;
            font-size: 18px;
            margin-bottom: 10px;
            font-family: 'Raleway';
            font-style: normal;
            font-weight: normal;
            line-height: 32px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
        .title > b {
            font-style: normal;
            font-size: 18px;
            font-family: 'Raleway';
            font-style: normal;
            font-weight: bold;
            line-height: 32px;
            letter-spacing: 0.05em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
    @media screen and (max-width: 1023px) and (min-width: 768px) {
        .team-g {
            .title {
                padding: 0 50px;
                margin-bottom: 40px;
            }
            .title > h1 {
                font-size: 49px;
                margin-left: 57px;
                margin-bottom: 20px;
                line-height: 54px;
            }
            .title > p {
                font-size: 16px;
                margin-bottom: 15px;
                line-height: 26px;
            }
            .title > b {
                font-size: 16px;
                line-height: 26px;
            }
        }
    }
    @media screen and (max-width: 767px){
        .team-g {
            .title {
                padding: 0 15px;
                margin-bottom: 50px;
            }
            .title > h1 {
                font-size: 32px;
                margin-left: 50px;
                margin-bottom: 20px;
                line-height: 48px;
            }
            .title > p {
                font-size: 14px;
                margin-bottom: 10px;
                line-height: 22px;
            }
            .title > b {
                font-size: 14px;
                line-height: 22px;
            }
        }
    }
