

.block-background{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.05;
    width: 327px;
    height: 779px;
    background: #7f4c3e;
}

.line{
    position: fixed;
    height: 376px;
    left: 71px;
    top: 173px;
    z-index: 115;
    border-left: solid 1px #FFFFFF;
    mix-blend-mode: difference;
}
.left-navigation{
    position: fixed;
    left: 62px;
    top: 600px;
    z-index: 115;
    mix-blend-mode: difference;
    & .g{
        width: 21px;
        display: block;
        height: 40px;
        margin-bottom: 35px;
    }

    & .b{
        display: block;
        width: 21px;
        height: 40px;
        margin-bottom: 35px;
    }
    & .s{
        display: block;
        width: 21px;
        height: 40px;
    }
}
@media screen and (max-width: 1023px) and (min-width: 768px) {
    .block-background{
        width: 149px;
        height: 400px;
    }
    .line{
        height: 190px;
        left: 25px;
        top: 92px;
        border-width: 1px;
    }
    .left-navigation {
        left: 16px;
        top: 302px !important;
        & .g{
            height: 30px;
            margin-bottom: 20px;
        }

        & .b{
            height: 30px;
            margin-bottom: 20px;
        }
        & .s{
            height: 30px;
        }
    }
}
@media screen and (max-height: 728px){
    .block-background{
        width: 149px;
        height: 400px;
    }
    .line{
        height: 190px;
    }
    .left-navigation {
        top: 402px;
        // & .g{
        //     height: 30px;
        //     margin-bottom: 20px;
        // }

        // & .b{
        //     height: 30px;
        //     margin-bottom: 20px;
        // }
        // & .s{
        //     height: 30px;
        // }
    }
}
@media screen and (max-width: 767px) {
    .block-background{
        width: 105px;
        height: 590px;
    }
    .line{
        display: none;
    }
    .left-navigation {
        display: none;
    }
}
