
    .view-about-us-g {
        padding-top: 208px;
        background-color: #FFFFFF !important;
        overflow: hidden;
    }
    .title {
        padding: 0 7.81vw;
        margin-bottom: 60px;
    }
    .title > h1 {
        font-weight: 600;
        font-style: normal;
        font-family: Oswald;
        font-size: 52px;
        margin: 0;
        margin-left: 96px;
        margin-bottom: 30px;
        line-height: 54px;
        letter-spacing: 0.02em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .title > p {
        font-weight: 400;
        font-style: normal;
        font-size: 18px;
        margin-bottom: 10px;
        font-family: Raleway;
        font-style: normal;
        font-weight: normal;
        line-height: 32px;
        letter-spacing: 0.05em;
        font-feature-settings: 'pnum' on, 'lnum' on;
        color: #000000;
    }
    .image-container-aboutUs{
        position: relative;
        margin-bottom: 60px;
        padding-left: 246px;

        .image{
            width: 100%;
            max-height: 570px;
            display: block;
        }

        .skew-image{
            position: absolute;
            bottom: -27px;
            right: 150px;
        }

        .text-image{
            position: absolute;
            left: -25px;
            bottom: 20px;
            width: 67.2vw;//67.3%
            height: 5.2vw;//83px
            background: #916C58;
            transform: skew(-25deg);
            text-align: right;
            font-family: 'Oswald';
            font-style: normal;
            font-weight: 500;
            font-size: 2.25vw;//36px
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #FFFFFF;
            &>p{
               line-height: 5.2vw;//83px
               margin: 0;
               margin-right: 60px;
               transform: skew(25deg);
            }
        }
    }
    .gallery-slider {
        position: relative;
        padding: 0 7.81vw;
    }
    .teams-slider {
        position: relative;
        padding: 60px 3vw;
    }
    .about-us-g-description {
        overflow: visible;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 15px;
        display: grid;
        margin-bottom: 60px;
        padding: 0 6.25vw;
        & ul {
            list-style: none;
            padding: 0;
            padding-left: 2.5em;
            & li {
                font-family: 'Raleway';
                font-style: normal;
                font-weight: normal;
                margin-bottom: 20px;
                font-size: 18px;
                line-height: 32px;
                letter-spacing: 0.05em;
                font-feature-settings: 'pnum' on, 'lnum' on;
            }
            & li::before {
                content: "\25A0";
                color: #916C58;
                font-weight: bold;
                font-size: 10px;
                display: inline-block;
                width: 1.5em;
                margin-left: -1.5em;
            }
        }
    }
    .our-team-aboutUs-container{
        background: rgba(145, 108, 88, 0.1);
        padding-top: 60px;
        // padding-bottom: 220px;
        .our-team-container_header{
            font-family: 'Oswald';
            height: 83px;
            color: #000000;
            font-weight: 600;
            font-size: 48px;
            display: flex;
            padding-left: 246px;
            align-items: baseline;
            justify-content: space-between;
            position: relative;
            padding-right: 150px;
            p {
                margin: 0;
            }
        }
        .all-our-trainers-link{
            margin: 56px 83px 76px 0;
            font-weight: bold;
            font-size: 12px;
            line-height: 14px;
            text-align: right;
            letter-spacing: 0.05em;
            -webkit-text-decoration-line: underline;
            text-transform: uppercase;
            // display: block;
            display:inline-block;
            color: #000000;
            &:hover {
                color: #916C58;
            }

            @media screen and (max-width:767px){

            }
        }

        .trainers-container .trainer-item:nth-of-type(even) {
            margin-top:25px;
        }
    }
    .certificate-title {
        padding: 0 7.81vw;
        margin-bottom: 60px;
        padding-top: 60px;
        h1 {
            font-weight: 600;
            font-style: normal;
            font-family: Oswald;
            font-size: 52px;
            margin: 0;
            margin-bottom: 40px;;
            text-align: center;
            line-height: 54px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
        p {
            font-weight: 400;
            font-style: normal;
            font-size: 18px;
            margin: 0;
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            line-height: 32px;
            letter-spacing: 0.05em;
            text-align: center;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }
    .certificate-description {
        // width: 80%;
        // margin-left: auto;
        // margin-right: auto;
        padding: 0 7.81vw;
        padding-bottom: 60px;
        padding-top: 60px;
        p {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 32px;
            font-feature-settings: 'pnum' on, 'lnum' on;
            color: #000000;
        }
    }

    @media screen and (max-width: 1023px) and (min-width: 768px) {
        .view-about-us-g {
            padding-top: 108px;
        }
        .title {
            padding: 0 50px;
            margin-bottom: 50px;
        }
        .title > h1 {
            font-size: 49px;
            margin-left: 57px;
            margin-bottom: 20px;
        }
        .title > p {
            font-size: 16px;
            line-height: 26px;
        }
         .image-container-aboutUs{
            margin-bottom: 50px;
            padding-left: 107px;

            .image{
                width: 100%;
                max-height: 316px;
            }

            .skew-image{
                width: 52px;
                height: 45px;
                right: 25px;
            }

            .text-image{
                position: absolute;
                left: -25px;
                bottom: 20px;
                width: 506px;//67.3%
                height: 56px;//83px
                font-size: 18px;//36px
                &>p{
                    line-height: 56px;//83px
                    margin-right: 30px;
                }
            }
        }
        .gallery-slider {
            padding: 0 50px;
        }
        .about-us-g-description {
            padding: 0 50px;
            margin-bottom: 50px;
            & ul {
                padding-left: 0.7em;
                & li {
                    font-size: 16px;
                    line-height: 26px;
                    margin-bottom: 15px;
                }
                & li::before {
                    font-size: 8px;
                }
            }
        }
        .our-team-aboutUs-container{
            background: rgba(145, 108, 88, 0.1);
            padding-top: 30px;


            .our-team-container_header{
                font-size: 45px;
                display: flex;
                padding-left: 50px;
                padding-right: 50px;
            }
            .trainers-container .trainer-item:nth-of-type(even) {
                margin-top:25px;
            }
        }
        .teams-slider {
            padding: 40px 30px;
        }
         .certificate-title {
            padding: 0 50px;
            margin-bottom: 30px;
            padding-top: 50px;
            h1 {
                font-size: 49px;
                margin-bottom: 30px;;
            }
            p {
                font-size: 14px;
                line-height: 22px;
            }
        }
        .certificate-description {
            padding: 0 50px;
            padding-bottom: 50px;
            padding-top: 50px;
            p {
                font-size: 14px;
                line-height: 22px;
            }
        }
    }
    @media screen and (max-width: 767px) {
        .view-about-us-g {
            padding-top: 108px;
        }
        .title {
            padding: 0 15px;
            margin-bottom: 50px;
        }
        .title > h1 {
            font-size: 32px;
            margin-left: 50px;
            margin-bottom: 20px;
        }
        .title > p {
            font-size: 14px;
            line-height: 22px;
        }
         .image-container-aboutUs{
            margin-bottom: 73px;
            padding: 0;

            .image{
                width: 100%;
                max-height: 214px;
            }

            .skew-image{
                width: 35px;
                height: 30px;
                right: 15px;
                bottom: -15px;
            }

            .text-image{
                text-align: left;
                bottom: -35px;
                width: 57.3%;//67.3%
                height: auto;//83px
                font-size: 18px;//36px
                &>p{
                    line-height: 24px;
                    margin-left: 45px;
                    margin-right: 0;
                }
            }
        }
        .gallery-slider {
            padding: 0 15px;
        }
        .about-us-g-description {
            padding: 0 15px;
            grid-template-columns: repeat(1, 1fr);
            margin-bottom: 50px;
            & ul {
                margin: 0;
                padding-left: 0.7em;
                & li {
                    font-size: 14px;
                    line-height: 22px;
                }
                & li::before {
                    font-size: 8px;
                }
            }
        }
        .our-team-aboutUs-container{
            background: rgba(145, 108, 88, 0.1);
            padding-top: 50px;
            .our-team-container_header{
                font-size: 24px;
                display: inline-block;
                padding-left: 37px;
                padding-right: 15px;
                width: 90%;
                & p {
                    text-align: right;
                }
            }
            .trainers-container .trainer-item:nth-of-type(even) {
                margin-top:25px;
            }
        }
        .teams-slider {
            padding: 37px 0 37px 15px;


            ::v-deep(.swiper-button-prev){
                @media screen and (max-width:767px){
                    background-image: url('../../assets/images/btn-slider-prev.svg');
                    top: -25px;
                    left: calc(100% - 155px) !important;
                    display: initial;
                    width: 66px;
                    height: 46px;

                    &::after{
                    display: none;
                    }
                }
            }

            ::v-deep(.swiper-button-next){
                @media screen and (max-width:767px){
                background-image: url('../../assets/images/btn-slider-next.svg');
                top: -25px;
                display: initial;
                width: 66px;
                height: 46px;

                    &::after{
                    display: none;
                    }
                }
            }

        }
         .certificate-title {
            padding: 0 15px;
            margin-bottom: 20px;
            padding-top: 50px;
            h1 {
                font-size: 32px;
                line-height: 48px;
                margin-bottom: 20px;;
            }
            p {
                font-size: 14px;
                line-height: 22px;
            }
        }
        .certificate-description {
            padding: 0 15px;
            padding-bottom: 50px;
            padding-top: 50px;
            p {
                font-size: 14px;
                line-height: 22px;
            }
        }
    }
