.make-order-popup .text-1{
    margin-bottom: 70px;
    width: 370px;
    font-family: Raleway;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    letter-spacing: 0.1em;
    font-feature-settings: 'pnum' on, 'lnum' on;
}


  .close {
    // background-image: url("../../assets/images/menu-close.svg");
    // background-repeat: no-repeat;
    width: 26px;
    height: 26px;
    position: absolute;
    right: 150px;
    top: 60px;
    &:hover {
        cursor: pointer;
        & svg > g{
            opacity: 1;
        }
        // filter: brightness(1);
    }

    @media screen and (max-width:767px){
        right: 25px;
        top: 25px;
    }
  }

