
@import 'swiper/swiper.min.css';
@import 'swiper/components/navigation/navigation.min.css';

.swiper-container {
    width: 100%;
    max-height: 380px;
    height: 100%;
    margin-bottom: 60px;
    position: unset;

    ::v-deep(.swiper-button-prev) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        left: 60px !important;
    }

    ::v-deep(.swiper-button-next) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        right: 60px !important;
    }
}



.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;


    &::after {
      content: url('../../assets/images/zoom-img.svg');
      display: none;
      position: absolute;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }

    &:hover::after {
        display: flex;
    }

    &:hover  img{
       filter: brightness(0.5);
    }
  }


.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition:filter .2s ease-out;
  }

  @media screen and (max-width: 1023px) and (min-width: 768px) {
      .swiper-container {
        margin-bottom: 110px;
        ::v-deep(.swiper-button-prev) {
            left: 20px !important;
        }

        ::v-deep(.swiper-button-next) {
            right: 20px !important;
        }
    }
  }

  @media screen and (max-width: 767px) {
      .swiper-container {
          margin-bottom: 110px;
        ::v-deep(.swiper-button-prev) {
            top: unset;
            bottom: -50px;
            left: 40% !important;
            font-weight: 900;
        }

        ::v-deep(.swiper-button-next) {
            top: unset;
            bottom: -50px;
            right: 40% !important;
            font-weight: 900;
        }
    }
  }



