
.trainer-item{
    transition: all .2s ease-in;
    position: relative;
    margin-bottom: 126px;

    &:nth-child(4n+4){
        transform-origin: right;
    }

    &:nth-child(4n+1){
        transform-origin: left;
    }

    &.is-scaled{
         transform:scale(1.3);
         z-index: 101;
    }
    .info{
        transform: none;
        position: absolute;
        display: block;
        width: 2.43vw;
        right: 10px;
        top: 10px;
        & .info-show {
            width: 100%;
            height: 100%;
            cursor: pointer;
            // background: #ACACAC;
            border-radius: 50%;
            // &:hover {
            // background: #916C58;
            // }
        }
        & .info-close {
            width: 100%;
            height: 100%;
            cursor: pointer;
            // background: #ACACAC;
            border-radius: 50%;
            // &:hover {
            // background: #916C58;
            // }
        }
    }

}

img{
    display:block;
    cursor: pointer;
    width:100%;
}

Video{
    display:block;
    cursor: pointer;
    width: 100%;
}


.firstName{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: left;
    padding: 1px 10px;
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 14px;
    color: #FFFFFF;
    position: absolute;
    width: 91px;
    height: 16px;
    left: 30px;
    bottom: -4px;
    background: #916C58;
}
.lastName{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: left;
    padding: 1px 10px;
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.05em;
    color: #FFFFFF;
    position: absolute;
    width: 91px;
    height: 16px;
    left: 30px;
    bottom: -25px;
    background: #916C58;
}
.descriptionTitle{
    font-family: Raleway;
    font-style: normal;
    font-weight: 800;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.05em;
    margin: 0;
    color: #000000;
}
.description{
    display: block;
    position: absolute;
    overflow-y: auto;
    max-height: 200px;
    left: 0;
    right: 0;
    padding: 0.94rem 1.88rem;
    font-family: Raleway;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 28px;
    color: #000000;


    & p {
        margin: 0;
        // max-height: 95px;
    }

    &::-webkit-scrollbar{
        width: 10px;
    }
    &::-webkit-scrollbar-track{
        margin-top: 15px;
        margin-bottom: -100px;
    }
    &::-webkit-scrollbar-thumb {
        background: url('../../assets/images/scroll.svg') no-repeat;
    }
}

.description-background {
    background-color: #F4F0EE;
}

@media screen and (max-width: 1023px) and (min-width: 768px) {
    .trainer-item{
        margin-bottom: 78px;

        &:nth-child(4n+4){
            transform-origin: center;
        }

        &:nth-child(4n+1){
            transform-origin: center;
        }

        &.is-scaled{
            transform:scale(1.2);
        }
    }
    .firstName{
        bottom: 4px;
        font-size: 10px;
        width: 75.17px;
        height: 13.88px;
    }
    .lastName{
        bottom: -17px;
        font-size: 10px;
        width: 75.17px;
        height: 13.88px;
    }
    .descriptionTitle{
        font-size: 12px;
        line-height: 16px;
    }
    .description{
        font-size: 12px;
        line-height: 22px;
        max-height: 161px;
    }
}

@media screen and (max-width: 767px) {
    .trainer-item{
        margin-bottom: 60px;

        &:nth-child(4n+4){
            transform-origin: center;
        }

        &:nth-child(4n+1){
            transform-origin: center;
        }

        &.is-scaled{
            transform:scale(1.1);
        }
    }
    .firstName{
        bottom: 4px;
        font-size: 10px;
        width: 75.17px;
        height: 13.88px;
    }
    .lastName{
        bottom: -15px;
        font-size: 10px;
        width: 75.17px;
        height: 13.88px;
    }
    .descriptionTitle{
        font-size: 12px;
        line-height: 16px;
    }
    .description{
        font-size: 12px;
        line-height: 22px;
        max-height: 161px;
    }
}





