
    .price-s {
        padding-top: 208px;
        background-color: #FFFFFF !important;
        li {
            transform: none !important;
        }
        .title {
            padding: 0 7.81vw;
            margin-bottom: 60px;
            & > h1 {
                font-weight: 600;
                font-style: normal;
                font-family: Oswald;
                font-size: 52px;
                margin: 0;
                margin-left: 96px;
                margin-bottom: 30px;
                line-height: 54px;
                letter-spacing: 0.02em;
                font-feature-settings: 'pnum' on, 'lnum' on;
                color: #000000;
            }
        }
        .price-direction {
            font-family: Oswald;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 27px;
            letter-spacing: 0.02em;
            font-feature-settings: 'pnum' on, 'lnum' on;
            padding: 0 7.81vw;
            padding-bottom: 60px;
            & > .direction {
                flex-wrap: wrap;
                list-style-type: none;
                display: flex;
                color: #000000;
                grid-column-gap: 40px;
                align-items: center;
                text-align: center;
                padding: 0;
                cursor: pointer;
                border-bottom: solid 2px #000000;
                & > li {
                    padding-bottom: 20px;
                    position: relative;
                    white-space: nowrap;
                    &:hover {
                        color: #916C58;
                    }
                }
            }
            .invert-color{
                color: #916C58;
                &::before {
                    content: "";
                    color: #916C58;
                    position: absolute;
                    bottom: -2px;
                    top: 0px;
                    left: 0;
                    right: 0;
                    border-bottom: 2px solid #916C58;
                }
            }
            & .price{
                color: #000000;
                font-feature-settings: 'pnum' on, 'lnum' on;
                font-style: normal;
                width: 50%;
                min-height: 47.8vh;
                margin-bottom: 60px;
                &>ul {
                    list-style-type: none;
                    padding: 0;
                    font-family: Raleway;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 28px;
                    letter-spacing: 0.05em;
                    font-feature-settings: 'pnum' on, 'lnum' on;
                    color: #000000;
                    &>li {
                        font-family: Raleway;
                        font-weight: normal;
                        width: 100%;
                        font-size: 14px;
                        line-height: 28px;
                        letter-spacing: 0.05em;
                        justify-content: space-between;
                        margin-bottom: 10px;
                        &>.list-discrip {
                            font-size: 12px;
                            line-height: 14px;
                            width: 80%;
                        }
                        &>div{
                            display: flex;
                            & .line-dotted {
                                flex: 1 0;
                                border-bottom: 2px dotted #000;
                                height: 20px;
                                margin: 0 .4em;
                            }
                            & .type-name {
                                margin: 0;
                                max-width: 316px;
                            }
                            & .type-price {
                                margin: 0;
                                font-weight: 600;
                            }
                        }

                    }
                }
            }
        }
    }


    @media screen and (max-width: 1023px) and (min-width: 768px){
        .price-s {
            padding-top: 108px;
            .title {
                padding: 0 50px;
                margin-bottom: 30px;
                & > h1 {
                    font-size: 49px;
                    margin: 0;
                    margin-left: 0;
                }
            }
            .price-direction {
                font-size: 14px;
                padding: 0 50px;
                & > .direction {
                    border-bottom: none;
                    text-align: left;
                    & li {
                        padding-bottom: 10px;
                    }
                }
                & .price{
                    text-align: left;
                    display: block;
                    width: 50%;
                }
            }
        }
    }
    @media screen and (max-width: 767px){
        .price-s {
            padding-top: 108px;
            .title {
                padding: 0 15px;
                margin-bottom: 20px;
                & > h1 {
                    font-size: 32px;
                    line-height: 48px;
                    margin: 0;
                    margin-left: 0;
                }
            }
            .price-direction {
                font-size: 14px;
                padding: 0 15px;
                & > .direction {
                    border-bottom: none;
                    text-align: left;
                    & li {
                        padding-bottom: 5px;
                        font-size: 12px;
                        line-height: 18px;
                    }
                }
                & .price{
                    text-align: left;
                    display: block;
                    width: 100%;
                    &>ul {
                        font-size: 12px;
                        line-height: 18px;
                        &>li {
                            font-size: 12px;
                            line-height: 18px;
                            &>.list-discrip {
                                font-size: 10px;
                                line-height: 14px;
                                max-width: 370px;
                            }
                            &>div{
                                display: flex;
                                & .line-dotted {
                                    height: 14px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .list-price-s-enter-active,
    .list-price-s-leave-active {
        transition: all .2s ease;
    }
    .list-price-s-enter-from,
    .list-price-s-leave-to {
        opacity: 0;
    }
