
@import 'swiper/swiper.min.css';
@import 'swiper/components/navigation/navigation.min.css';

.swiper-container {
    width: 100%;
    max-height: 380px;
    height: 100%;
    border-top: solid 2px #F3EFEC;
    border-bottom: solid 2px #F3EFEC;
    ::v-deep(.swiper-button-prev) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        left: 62px !important;
    }
    ::v-deep(.swiper-button-next) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        right: 58px !important;
    }
    ::v-deep(.swiper-wrapper) {
        left: 250px;
    }
}



.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: solid 1px #F3EFEC;
    border-right: solid 1px #F3EFEC;
    padding: 40px;
    max-width: 18.7vw !important;
    height: auto;
    &::after {
        content: url('../../assets/images/zoom-img.svg');
        display: none;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.1);
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
    &:hover::after {
        display: flex;
    }
  }

.swiper-slide-prev {
    pointer-events: none;
    &::after {
        content: '';
        position: unset;
        display: none;
        background-color: unset;
        width: unset;
        height: unset;
    }
    & img {
        opacity: 0.5;
    }
}

.swiper-slide-next + .swiper-slide + .swiper-slide {
    pointer-events: none;
    &::after {
        content: '';
        position: unset;
        background-color: unset;
        width: unset;
        height: unset;
        display: none;
    }
    & img{
        opacity: 0.5;
    }
  }

// .isOpacity {
//    opacity: 0.5;
// }

.swiper-slide img {
    display: block;
    max-width: 18.7vw !important;
    height: auto;
    object-fit: cover;
  }

@media screen and (max-width: 1023px) and (min-width: 768px) {
    .swiper-container {
        ::v-deep(.swiper-button-prev) {
            display: none;
        }
        ::v-deep(.swiper-button-next) {
            display: none;
        }
        ::v-deep(.swiper-wrapper) {
            left: 100px;
        }
    }
  }

@media screen and (max-width: 767px) {
    .swiper-container {
        ::v-deep(.swiper-button-prev) {
            display: none;
        }
        ::v-deep(.swiper-button-next) {
            display: none;
        }
        ::v-deep(.swiper-wrapper) {
            left: 100px;
        }
    }
    .swiper-slide-next + .swiper-slide {
        pointer-events: none;
        &::after {
            content: '';
            position: unset;
            background-color: unset;
            width: unset;
            height: unset;
            display: none;
        }
        & img{
            opacity: 0.5;
        }
    }
  }




