
.breadcrumb {
    position: absolute;
    right: 68px;
    top: 165px;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;
    text-align: right;
    letter-spacing: 0.05em;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #000000;
    opacity: 0.75;
    z-index: 100;
    & div {
        & span {
            margin: 0 30px;
            & a {
                text-decoration: unset;
                color: unset;
            }
            &:last-child {
                opacity: 0.25;
                margin-right: 0;
            }
        }
    }
}
@media screen and (max-width: 1023px) and (min-width: 768px) {
    .breadcrumb {
        right: 50px;
        top: 87px;
        font-size: 10px;
        & div {
            & span {
                margin: 0 20px;
                & a {
                    text-decoration: unset;
                    color: unset;
                }
            }
        }
    }
}
@media screen and (max-width: 768px) {
    .breadcrumb {
        right: 15px;
        top: 78px;
        font-size: 10px;
        & div {
            & span {
                margin: 0 20px;
                & a {
                    text-decoration: unset;
                    color: unset;
                }
            }
        }
    }
}
