
    .scrollTop {
        z-index: 250;
        position: fixed;
        bottom: 80px;
        right: 10px;
        mix-blend-mode: difference;

        &>.scroll-text{
            font-family: Raleway;
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            position: absolute;
            color: #FFFFFF;
            bottom: 18px;
            right: -20px;
            margin: 0;
            transform: translateX(-50%) rotate(-90deg);

        }

        &>svg{
            height: 61px;
            width: 16px;
            display: block;
            transition: all .2s ease-in;
        }
        &:hover{
            cursor: pointer;
        }
        &:hover > svg{
            // height: 80px;
            transform: scaleY(1.30);
            transform-origin: bottom;
        }
    }
